export default {
  props: {
    tableData: Array,
    tableLabel: Array,
    config: Object
  },

  data() {
    return {};
  },

  methods: {
    editUser(row) {
      this.$emit('editUser', row);
    },

    deleteUser(row) {
      this.$emit('deleteUser', row);
    },

    changePage(page) {
      this.$emit('changePage', page);
    }

  }
};